#root {
  display: flex;
  flex-direction: column;
  justify-items: center;

  width: fit-content;
  margin: 2.5em auto;
  padding: 0 20px 10px;

  box-shadow: 0 0 10px 10px #882222;
  border-radius: 20px;
}

header {
  display: flex;

  width: fit-content;
  margin: auto;
}

#info, #info * {
  display: flex;
  justify-content: space-between;
}

#info img {
  border-radius: 50%;
  box-shadow: 0 0 5px 2px rgb(136 34 34 / 40%),
      inset 0 0 10px 5px rgb(136 34 34 / 60%);
}

img {
  height: 25px;
  margin: auto 10px;
  pointer-events: none;
}

img.bombs-counter {
  height: 15px;
  margin: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .logo {
      animation: logo-spin infinite 20s linear;
  }
}

@keyframes logo-spin {
  from {
      transform: rotate(0deg);
  }
  to {
      transform: rotate(360deg);
  }
}
